import React, { useState } from "react";
import ReactPageScroller from "react-page-scroller";
import Page1 from "./Pages/Page1";
import Page2 from "./Pages/Page2";
import Page3 from "./Pages/Page3";

const Checkout = () => {
  return (
    <>
      <ReactPageScroller>
        <Page1 />
        <Page2 />
        <Page3 />
      </ReactPageScroller>
    </>
  );
};
export default Checkout;
